@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
    .apolline {
        font-family: apolline, serif;
    }

    .chonk {
        font-family: 'Rubik Glitch', cursive;
    }

}

@layer base {
    html {
        scroll-behavior: smooth;
    }

    * {
        font-family: interstate, sans-serif;
    }
}
